@import "vars";

.secBasic01 {
    background-size: cover;
    background-repeat: no-repeat;
    background-position: left;
    height: 300px;
    display: flex;

    @include mq(md) {
        padding: 0 15px;
        height: 182px;
    }

    &school {
        background-image: url(../img/img24@2x.png);

        @include mq(md) {
            background-image: url(../img/img25@2x.png);
        }
    }

    &schola {
        background-image: url(../img/img39@2x.png);

        @include mq(md) {
            background-image: url(../img/img40@2x.png);
        }
    }

    &curriculum {
        background-image: url(../img/img47@2x.png);

        @include mq(md) {
            background-image: url(../img/img48@2x.png);
        }
    }

    &developer {
        background-image: url(../img/img49@2x.png);

        @include mq(md) {
            background-image: url(../img/img50@2x.png);
        }
    }

    &contact {
        background-image: url(../img/img02@2x.png);

        @include mq(md) {
            background-image: url(../img/img03@2x.png);
        }
    }

    &news {
        background-image: url(../img/img55@2x.png);

        @include mq(md) {
            background-image: url(../img/img56@2x.png);
        }
    }

    &company {
        background-image: url(../img/img10@2x.png);

        @include mq(md) {
            background-image: url(../img/img11@2x.png);
        }
    }

    &privacy {
        background-image: url(../img/img12@2x.png);

        @include mq(md) {
            background-image: url(../img/img13@2x.png);
        }
    }

    &sitemap {
        background-image: url(../img/img14@2x.png);

        @include mq(md) {
            background-image: url(../img/img15@2x.png);
        }
    }

    >.container {
        @include mq(md) {
            width: 100%;
        }

        >.txt {
            margin: auto;
            margin-left: 0;

            >.title {
                font-size: 52px;
                line-height: 75px;
                color: #232448;
                margin: 0;

                @include mq(md) {
                    font-size: 25px;
                    font-weight: 600;
                    line-height: 36px;
                    margin-bottom: 10px;
                }
            }

            >.subtitle {
                font-family: 'Cormorant Garamond';
                font-size: 30px;
                font-weight: bold;
                line-height: 34px;
                color: #232448;

                @include mq(md) {
                    font-size: 17px;
                    line-height: 19px;
                }
            }
        }
    }
}